import React from 'react';
import {
  Button,
  Segmented,
  Space,
  Divider,
} from 'antd';
import {
  AppstoreOutlined,
  BarsOutlined,
  CloudDownloadOutlined,
  CloudUploadOutlined,
  DeleteOutlined,
  SyncOutlined,
} from '@ant-design/icons';
import CreateDirBtn from '../CreateDirBtn';
import UploadFileBtn from '../UploadFileBtn';
import TaskCenterBtn from '../TaskCenterBtn';
import styles from './index.less';

function ToolBar() {
  const isMult = false;
  return (
    <Space size={8}>
      {isMult ? (
        <>
          <Button type="primary" icon={<CloudDownloadOutlined />}>批量下载</Button>
          <Button type="primary" ghost icon={<DeleteOutlined />}>批量删除</Button>
        </>
      ) : (
        <>
          <UploadFileBtn />
          <CreateDirBtn />
        </>
      )}
      <Button icon={<SyncOutlined />}>刷新</Button>
      <Divider type="vertical" />
      <Segmented options={[{ value: 'list', icon: <BarsOutlined /> }, { value: 'card', icon: <AppstoreOutlined /> }]} />
      <TaskCenterBtn />
    </Space>
  );
}

export default ToolBar;
